import React, { useCallback } from 'react';
import { Card, Avatar, Button } from 'antd';
import Link from 'next/link';
import { useSelector, useDispatch } from 'react-redux';
import { LOG_OUT_REQUEST } from '../reducers/user';

const UserProfile = () => {
    
    const dispatch = useDispatch();
    const { me } = useSelector( state => state.user );

    const onLogout = useCallback(() => {
        dispatch({
            type: LOG_OUT_REQUEST,
        });
    }, []);

     console.log(" UserProfile me : ", me)

    
    //  next.js  prefetch    .      prefetch  ,      
    //     ,       
 
    return (
        <Card 
            actions={[
                <Link href='/profile'  prefetch  key='twit'>
                    <a>
                       <div key="twit"><br/> {me.Posts && me.Posts.length}</div>
                    </a>
                </Link>,
                <Link href='profile' prefetch  key='following'>
                    <a>
                        <div key="followings"><br/> { me.Followings && me.Followings.length}</div>
                    </a>
                </Link>,
                <Link href='/profile' prefetch  key='follower'>
                    <a>
                        <div key="followers"><br/> {me.Followings && me.Followers.length}</div>
                    </a>
                </Link>,
            ]}
        >
            <Card.Meta
                 avatar={<Avatar>{me.nickname && me.nickname[0]}</Avatar>}
                 title={me.nickname}
            />
            <Button onClick={onLogout}></Button>
        </Card>
    );
};

export default UserProfile;